.page {
  overflow-x: hidden;

}

.page__body {
  min-width: 420px;
  min-height: 100%;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 0.875em;
  font-weight: 400;
  line-height: 1.5;
  background-color: $smoke-white;
  color: $black;
  overflow-x: hidden;
}

.page-main {
  @media (min-width: $desktop-width) {
    padding-left: 320px;
    margin-top: 100px;
  }
}
